<template>
  <div>
    <b-container>
      <b-form>
        <b-row class="mt-2">
          <b-col sm="6" offset="2" class="mx-auto">
            <b-input
              v-model="id"
              class="mb-2 mr-sm-2 mb-sm-0 input"
              placeholder="Unesite ID telefona"
            ></b-input>
            <b-button variant="primary" size="lg" @click="search">Search reviews</b-button>
          </b-col>
        </b-row>
      </b-form>
    </b-container>
    <b-table
      sticky-header="800px"
      :items="reviews"
      head-variant="light"
    ></b-table>
    <b-form>
      <b-row class="mt-2">
        <b-col lg="6" offset="2" class="text-center mx-auto">
          <b-input
            v-model="user"
            class="mb-2 mr-sm-2 mb-sm-0 mb-2 input"
            placeholder="Unesite ime"
          ></b-input>
          <b-input
            v-model="ocena"
            class="mb-2 mr-sm-2 mb-sm-0 mb-2 input"
            placeholder="Unesite ocenu"
          ></b-input>
          <b-button variant="primary" size="lg" @click="addNew">Save</b-button>
        </b-col>
      </b-row>
    </b-form>
      
  </div>
</template>

<script>

import router from "@/router";
import { mapState, mapActions } from 'vuex';

export default {
  name: "CommentsList",
  computed: {
            ...mapState(['reviews'])
        },

  data() {
    return {
      id: '',
      user: '',
      ocena: ''
    };
  },
  methods: {
     ...mapActions(['load_reviews', 'new_review']),

    search: function () {
      this.load_reviews(this.id);
    },

    addNew: function () {
       const review = JSON.stringify({idTelefona: this.id, user: this.user, ocena: parseInt(this.ocena) });
       this.new_review(review);

    },
  },
};
</script>

<style scoped>
.input {
  margin-bottom: 10px !important;
}
</style>