import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const apiBaseUrl = 'http://localhost:8080';

export default new Vuex.Store({
  state: {
    mobiles: [],
    reviews: []
  },

  mutations: {
    set_mobiles: function (state, mobiles) {
      state.mobiles = mobiles;
    },

    set_reviews: function (state, reviews) {
      state.reviews = reviews;
    },

    add_mobile: function (state, mobile) {
      state.mobiles.push(mobile);
    },

    add_review: function (state, review) {
      state.reviews.push(review);
    },

    remove_mobile: function (state, id) {
      for (let m = 0; m < state.mobiles.length; m++) {
        if (state.mobiles[m].id === id) {
          state.mobiles.splice(m, 1);
          break;
        }
      }
    },

    update_mobile: function (state, payload) {
      for (let m = 0; m < state.mobiles.length; m++) {
        if (state.mobiles[m].id === parseInt(payload.id)) {
          state.mobiles[m].model = payload.mob.model;
          state.mobiles[m].producer = payload.mob.producer;
          break;
        }
      }
    }
  },

  actions: {
    load_mobiles: function ({ commit }) {
      
      fetch(apiBaseUrl + '/api/telefon', { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('set_mobiles', jsonData)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    load_reviews: function ({ commit }, idTelefona) {
      
      fetch(apiBaseUrl + `/api/ocena/${idTelefona}`, { method: 'get'}).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('set_reviews', jsonData)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    delete_mobile: function({ commit }, id) {
      fetch(apiBaseUrl + `/api/telefon/${id}`, { method: 'delete' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('remove_mobile', jsonData.id)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    new_mobile: function({ commit }, mobile) {
      
      fetch(apiBaseUrl + '/api/telefon', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: mobile
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('add_mobile', jsonData);
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    new_review: function({ commit }, review) {
      
      fetch(apiBaseUrl + '/api/ocena', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: review
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('add_review', jsonData);
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    change_mobile: function({ commit }, payload) {
      fetch(apiBaseUrl + `/api/telefon/${payload.id}`, {
        method: 'put',
        headers: {
          'Content-Type': 'application/json'
        },
        body: payload.mob
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('update_mobile', {id: payload.id, mob:jsonData});
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    }
  }
})
