<template>
    <b-container fluid>
        <b-form>
            <b-row class="mt-2">
                <b-col sm="4" offset="2">
                    <b-input v-model="newProducer" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Upisite novu marku"></b-input>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col sm="4" offset="2">
                    <b-input v-model="newModel" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Upisite novi model"></b-input>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col sm="4" offset="2">
                    <b-input v-model="newMemory" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Upisite novu memoriju"></b-input>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col sm="4" offset="2">
                    <b-input v-model="newColor" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Upisite novu boju"></b-input>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col sm="4" offset="2">
                    <b-input v-model="newPrice" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Upisite novu cenu"></b-input>
                </b-col>
            </b-row>
            <b-row class="mt-2">
                <b-col sm="8">
                    <b-button variant="primary" size="lg" @click="addNew">Save</b-button>
                </b-col>
              </b-row>
        </b-form>
        
    </b-container>
    
</template>

<script>
    import { mapActions } from 'vuex';
    const Joi = require('joi');

    const schema = Joi.object().keys({
    marka: Joi.string().max(20).required(),
    model: Joi.string().min(1).max(20).required(),
    memorija: Joi.string().min(3).required(),
    boja: Joi.string().max(15).required(),
    cena: Joi.number().positive().greater(30).required()
    });

    export default {
        name: "EditMobile",
        
        props: {
            marka: {
                type: String,
                default: ''
            },
            model: {
                type: String,
                default: ''
            },
            memorija: {
                type: String,
                default: ''
            },
            boja: {
                type: String,
                default: ''
            },
            cena: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                newProducer: '',
                newModel: '',
                newMemory: '',
                newColor: '',
                newPrice: '',
                id:''
            }
        },
        mounted: function () {
            this.newProducer = this.marka;
            this.newModel = this.model;
            this.newMemory = this.memorija;
            this.newColor = this.boja;
            this.newPrice = this.cena;
        },
        methods: {
            ...mapActions(['new_mobile', 'change_mobile']),

            addNew: function() {
                const mob = JSON.stringify({marka: this.newProducer, model: this.newModel,  memorija: this.newMemory, boja: this.newColor, cena: parseInt(this.newPrice)});
                
                let { error } = schema.validate(mob);

               if(error){
                  alert(error.details[0].message);
                }
                else{
                  if (!this.$route.params.id)
                     this.new_mobile(mob);
                  else{
                    this.change_mobile({id: this.$route.params.id, mob:mob});
                  }
                  this.newModel = '';
                  this.newProducer = '';
                  this.newColor = '';
                  this.newMemory = '';
                  this.newPrice = 0;
                }
            }
        }
    }
</script>

